import { IStudioModelBasics } from "../domain/infrastructure/IStudioModelBasics";
import { ModelType } from "../../qCloudTemp/quickCloud";

export class StudioModelBasicsImpl implements IStudioModelBasics {
  public getModelIdFromPathStructure(value: string, modelType: ModelType) {
    const regExp = new RegExp(`<<${modelType}:(\\w(?![/]).*)>>`);
    const id = regExp.exec(value)![1];
    return id;
  }
  public createPathStructureByModelId(id: string, modelType: ModelType) {
    return `<<${modelType}:${id}>>`;
  }
}