\n\n// async function selectItem(item: IModel) { //Orbay: Bu kısmı (metodu olduğu gibi) tam anlamadım. Üzerine konuşalım.\n//   selectedItem.value = item;\n//   const studioModelBasics = new StudioModelBasicsImpl();\n//   let targetItem = viewModel.studio.items.find(i => i.name === item.name);\n//   const text = studioModelBasics.createPathStructureByModelId(targetItem.ID, \"woff\");\n//   studio.insertTextToEditor(text, { position: \"cursor\" });\n// }\n\nasync function selectItem(item: IModel) {\n  selectedItem.value = item;\n  const studioModelBasics = new StudioModelBasicsImpl();\n  \n  let targetItem = viewModel.studio.items.find(i => i.name === item.name);\n  let text = \"\";\n  \n  if (targetItem) {\n    if (item.modelType === \"woff\") {\n      text = studioModelBasics.createPathStructureByModelId(targetItem.ID, \"woff\"); \n    } else if (item.modelType === \"woff2\") {\n      text = studioModelBasics.createPathStructureByModelId(targetItem.ID, \"woff2\");\n    } else if (item.modelType === \"ttf\") {\n      text = studioModelBasics.createPathStructureByModelId(targetItem.ID, \"ttf\");\n    }\n    \n    \n    \n    studio.insertTextToEditor(text, { position: \"cursor\" });\n  }\n}\n\nfunction setFontItems() {\n  viewModelManager.traverseChildren(viewModel.studio.items, (item, index) => {\n    if (item.objectType == \"model\" && item.modelType == \"woff\") { items.value.push(item); }\n    if (item.objectType == \"model\" && item.modelType == \"woff2\") { items.value.push(item); }\n    if (item.objectType == \"model\" && item.modelType == \"ttf\") { items.value.push(item); }\n    return false;\n  });\n}\n\nonMounted(() => {\n  setFontItems();\n});\n\n</script>\n\n<style scoped>\n.list {\n  list-style-type: none;\n  padding: 0;\n}\n\n.list-dialog {\n  max-height: 424px;\n  overflow: auto;\n}\n\n.list-item {\n  cursor: pointer;\n  padding: 8px;\n  border-bottom: 1px solid #ccc;\n}\n\n.container {\n            padding: 32px 20px 20px;\n    }\n\n.list-item:last-child {\n  border-bottom: none;\n}\n</style>\n"],"sourceRoot":""}]);
